
input {
  font-size: 100%;
  background: inherit;
  border: none;
}
.terminal-line {
  min-height: 22px;
}
@media screen and (min-width: 1800px) {
.terminal-line {
    max-width: 70%;
}
}
.terminal-line > * {
  display: inline-block;
  vertical-align: middle;
  text-align: start;
}
.terminal {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-y: auto;
  padding: 15px;
  margin-right: auto;
}
.sep {
  color: #b48ead;
}
.yellow {
  color: #ebcb8b;
}




